.popup__close:after, .popup__close:before {
  content: "";
  width: 100%;
  height: .125rem;
  background-color: #fff;
  transition: all .3s;
  position: absolute;
  right: 0;
}

.popup__close:after {
  bottom: calc(50% - .0625rem);
  transform: rotate(45deg);
